
section {
    margin-bottom: 40px;
  }
  
  h2 {
    color: #333;
  }
  
  p {
    color: #666;
  }
  

.project-card {
    display: flex;
    border: 1px solid #ddd;
    border-radius: 5px;
    overflow: hidden;
    margin-bottom: 20px;
  }
  
  .project-card img {
    width: 200px; 
    height: 150px; 
    object-fit: cover;
  }
  
  .project-details {
    padding: 10px;
  }
  
  .project-details h3 {
    color: #333;
  }
  
  .project-details p {
    color: #666;
  }
  
  .project-details a {
    color: #007bff; 
    text-decoration: none;
  }
  
  .project-details a:hover {
    text-decoration: underline;
  }
  

  