

.about-content {
    /* Adjust padding and margins for content spacing */
    padding: 2rem;
    margin: 0 auto;  /* Center the content horizontally */
    max-width: 800px;  /* Limit content width for larger screens (optional) */
  }
  
  .about-content h2 {
    font-size: 1.8rem;
    margin-bottom: 1rem;
    text-align: center;  /* Center align the heading */
  }
  
  .about-content p {
    line-height: 1.5;
    /* Adjust font size for readability */
    font-size: 16px;
  }
  